import React, { Component } from 'react'
import {connect} from 'react-redux'
import {stepLoaded} from '../../../actions'
import './offer.scss'
class  Offer extends Component {
  nextStep = () =>{
    const {stepLoaded} = this.props
    if(localStorage.getItem('token')){
      stepLoaded(2)
    } else {
      stepLoaded(1)
    }
  }
  render () {
    return (
      <div className="offer">
        <h1 className="offer-title">Просто. Удобно. Надежно.</h1>
        <div className="offer__item">
          <img src={require('../../../assets/img/offer1.png')}/>
          <div className="offer__item__content">
            <p className="offer__item__content-title">Размести 
              </p>
             <p className="offer__item__content-title offer__item__content-title--margin">предложение.</p> 
            <p className=" offer__item__content-title offer__item__content-title--red">Один раз и для всех. </p> 
            <p className="offer__item__content-text">Предложи свои услуги охотникам со всего мира. Больше не нужно ежедневно размещать объявления на разных форумах.</p>
            <button className="offer__item__content-button" onClick={this.nextStep}><span>+</span> Подключиться</button>
          </div>
        </div>
        <div className="offer__item offer__item__reverse">
          <img src={require('../../../assets/img/offer2.png')}/>
          <div className="offer__item__content offer__item__content__reverse">
            <p className="offer__item__content-title offer__item__content-title-reverse">Просматривай и подтверждай брони <span>онлайн.</span></p>
            <p className="offer__item__content-text offer__item__content-text-reverse">Больше не нужно согласовывать детали по телефону. Охотники могут бронировать только свободные даты и доступную дичь.</p>
            <button className="offer__item__content-button" onClick={this.nextStep}><span>+</span> Подключиться</button>
          </div>
        </div>
        <div className="offer__item">
          <img src={require('../../../assets/img/offer3.png')}/>
          <div className="offer__item__content">
              <p className="offer__item__content-title">Зарабатывай без комиссий со <span>100% предоплатой.</span></p>
              <p className="offer__item__content-text">Гарантия оплаты даже в случае отмены охоты. Больше никаких денежных споров.</p>
              <button className="offer__item__content-button" onClick={this.nextStep}><span>+</span> Подключиться</button>
          </div>
        </div>
      </div>
    )
  }
  
}
export default connect(null, {stepLoaded})(Offer);